<script>
import axios from 'axios'

export default {
  name: "VehicleNations",
  data() {
    return {
      vehicle_nations: null,
      filtered_nation_list: []
    }
  },

  methods: {
    fetch_nation_list() {
      axios
      .get("https://api.worldoftanks.com/wot/encyclopedia/info/?application_id=7362aa6d7cc838899f3a91d25d76625d"
      )
      .then(response => {
        //check search error
        if (this.check_api_error(response.data.status)) {
          this.vehicle_nations = response.data.data.vehicle_nations
          this.filter_vehicle_nations()
        }
        else {
          //handle if error with query
        }
      })
      .catch(error => console.log(error))
    },

    //check / remove nulls
    filter_vehicle_nations() {
      for (let each_nation in this.vehicle_nations) {
        if (this.vehicle_nations[each_nation]) {
          this.filtered_nation_list.push(this.vehicle_nations[each_nation])
        }
      }
    },

    //check for returned api errors
    check_api_error(the_response) {
      return (the_response === "ok") ? true : false
    }
  },

  mounted() {
    this.fetch_nation_list()
  }
}
</script>