<template>
  <div class="card">
    
    <header class="card_heading_switch_trees">
      <ul class="tab_heads">
        <li
          class="tab_head"
          v-for="tab in trees"
          :key="tab"
          :class="{'tab_head_switch_trees_active': active_tab === tab}"
          @click="switch_tab(tab)"
        >
          <slot :name="tab_head_slot_name(tab)"> 
            {{ tab }} 
          </slot>
        </li>
      </ul>
    </header>

    <main>
      <div class="tab_panel">
        <slot :name="tab_panel_slot_name"></slot>
      </div>
    </main>

  </div>
</template>

<script>
  
export default {
  name: "SwitchTrees",
  props: {
    initial_tree: String,
    trees: Array
  },

  data() {
    return {
      active_tab: this.initial_tree
    };
  },

  computed: {
    tab_panel_slot_name() {
      return `tab_panel_${this.active_tab}`
    }
  },

  methods: {
    tab_head_slot_name(tab_name) {
      return `tab_head_${tab_name}`
    },

    switch_tab(tab_name) {
      this.active_tab = tab_name
    }
  }
}
</script>

<style>
.card_heading_switch_trees {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  color: #fff;
  padding: 15px 6em 0;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
}

.tab_heads {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
  margin-left: 6px;
}

.tab_head {
  padding: 5px 18px;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  position: relative;
  cursor: pointer;
}

.tab_head_switch_trees_active {
  background-color: #C24440;
  color: #000;
  transition: 0.4s;
}
</style>
