<template>
  <div id="main_content">

    <!-- detailed stats overlay of selected tank -->
    <section id="stats_container">
      <div id="stats">
        <div v-for="tank_stats in this.tank_stats" :key="tank_stats.tank_id">
          <!-- tank image -->
          <img class="stats_tank_icon" :src="tank_stats.images.big_icon" :alt="tank_stats.name">
            <!-- tank name -->
            <h1 v-bind:class="[tank_stats.is_premium ? 'tank_header_premium' : 'tank_header']">
              {{ tank_stats.name }}
            </h1>

            <div class="stats_column_container">
              <!-- left column -->
              <div class="stats_column_item_left">
                <!-- vehicle nation -->
                <p>
                  {{ tank_stats_sections[0] }} {{ tank_stats.nation.toUpperCase() }}
                </p>
                <!-- vehicle type -->
                <p>
                  {{ tank_stats_sections[1] }} {{ tank_stats.type.toUpperCase() }}
                </p>
                <!-- vechicle tier -->
                <p>
                  {{ tank_stats_sections[2] }} {{ tank_stats.tier }}
                </p>
                <!-- vehicle cost either gold / credit -->
                <p v-if="!check_tank_currency_type(tank_stats.price_gold)">
                  {{ tank_stats_sections[3] }} {{ tank_stats.price_gold.toLocaleString() }}
                </p>
                  <p v-else>
                    {{ tank_stats_sections[4] }} {{ tank_stats.price_credit.toLocaleString() }}
                  </p>
              </div>

              <!-- right column -->
              <div class="stats_column_item_right">
                
              </div>

            </div> <!-- stats_column_container -->

        </div>
      </div>
    </section>

    <section id="overlay">
      <div id="grid_container" class="grid_container">
        <!-- Tier bar component -->
        <div class="tier_nav_container" v-for="item in tank_tiers" :key="item">
          <div class="tier_nav">
            {{ item }}
          </div>
        </div>

        <div v-for="(each_tier, index) in tier_divs" v-bind:key="index">
          <div v-for="data in parentData" v-bind:key="data.name">
            <div v-if="(data.tier - 1) === index">
              <!-- each tank container -->
              <div class="each_tank" v-bind:id="[data.is_premium ? 'each_p_tank' : 'each_tank']">
                <!-- tank name header -->
                <div :id="data.tank_id" class="tank_name">
                  {{ data.name }}
                </div>
                <!-- each tank img -->
                <img :id="data.tank_id" class="tank_images" :src="data.images.big_icon" :alt="data.name"/>
              </div>
            </div>
          </div>
        </div>
      </div> <!-- grid_container -->
    </section>

  </div>
</template>

<script>
export default {
  name: "TankSlot",
  props: {
    parentData: Object
  },
  data() {
    return {
      tank_stats_sections: [
        "Nation: ",
        "Type: ",
        "Tier: ",
        "Gold Price: ",
        "Credit Price: "
      ],
      tank_stats: [],
      converted_price: null,
      visible: false,
      tier_divs: 10,
      tank_tiers: ["I", "II", "III", "IV", "V", "VI", "VII", "VIII", "IX", "X"]
    }
  },

  methods: {
    check_tank_currency_type(currency) {
      return currency == 0 ? true : false
    },

    add_listener() {
      let the_tank = document.getElementsByClassName('each_tank')

      for (let iterator = 0; iterator < the_tank.length; iterator++) {
        the_tank[iterator].addEventListener('click', this.add_tank_id, false)
      }
    },

    //display clicked tank id in console for now
    add_tank_id(tank_id) {
      this.overlay_on()
      this.overlay_off()
      //not null
      if (tank_id.target.id) {
        this.get_clicked_tank_id(tank_id.target.id)
      }
    },

    get_clicked_tank_id(tank_id) {
      for (let each_tank in this.parentData) {
        if (this.parentData[each_tank].tank_id == tank_id) {
          this.tank_stats.push(this.parentData[each_tank])
        }
      }
    },

    overlay_on() {
      document.getElementById('stats').style.display = "block"
      document.getElementById('overlay').className = "overlay_on"
    },

    overlay_off() {
      window.addEventListener('click', function(e) {   
        if (!document.getElementById('grid_container').contains(e.target)) {
          document.getElementById('stats').style.display = "none"
          document.getElementById('overlay').className = "overlay_off"
        } 
      })
      //reset current tank stats array
      this.tank_stats = []
    }
  },

  mounted() {
    this.add_listener()
  },

  //remove listeners
  unmount() {
    let the_tank = document.getElementsByClassName('each_tank')

    for (let iterator = 0; iterator < the_tank.length; iterator++) {
      the_tank[iterator].removeEventListener('click', false)
    }
  }
}

</script>

