<template>
  <div id="container">
    
    <div v-if="hidden">
      <LoadingSpinner/>
    </div>

    <div v-else>
      <div class="welcome_container">
        <h1 id="welcome">{{ banner_header }}</h1>
        <p v-for="(welcome_message, index) in banner_description" :key="index">
          {{ welcome_message }} <br><br>
        </p>
      </div>
      <!-- tiles -->
      <NationTiles/>
      <Footer/>
    </div>

  </div>
