<script>
// @ = /src
import LoadingSpinner from '@/components/misc/LoadingSpinner'
import NationTiles from '@/views/NationTiles'
import Footer from '@/views/Footer'

export default {
  name: "Home",
  components: {
    LoadingSpinner,
    NationTiles,
    Footer
  },
  data() {
    return {
      hidden: true,
      banner_header: "Welcome",
      banner_description: [
        "Wotree is a site created for users of the popular World of Tanks game. It allows users to view nation tech trees as well as detailed stats of each tank in that nation.",
        "To view tech trees, click from either the below nation flags or use the navigation dropdown at top of page."
      ]
    }
  },
  methods: {
    show() {
      setTimeout(() => this.hidden = false, 1000);
    }
  },

  mounted() {
    this.show()
  }
}
</script>
