import { createRouter, createWebHistory } from 'vue-router'
import Home from '@/views/Home.vue'
import USSR from '@/views/nations/U.S.S.R..vue'
import USA from '@/views/nations/U.S.A..vue'
import UK from '@/views/nations/U.K..vue'
import Germany from '@/views/nations/Germany.vue'
import France from '@/views/nations/France.vue'
import Sweden from '@/views/nations/Sweden.vue'
import Japan from '@/views/nations/Japan.vue'
import China from '@/views/nations/China.vue'
import Czechoslovakia from '@/views/nations/Czechoslovakia.vue'
import Italy from '@/views/nations/Italy.vue'
import Poland from '@/views/nations/Poland.vue'

const routes = [
  { path: "/", name: "Home", component: Home },
  { path: "/U.S.S.R.", name: "U.S.S.R.", props: { nation_header: "U.S.S.R.", nation_query: "ussr" }, component: USSR },
  { path: "/U.S.A.", name: "U.S.A.", props: { nation_header: "U.S.A.", nation_query: "usa" },  component: USA },
  { path: "/U.K.", name: "U.K.", props: { nation_header: "U.K.", nation_query: "uk" }, component: UK },
  { path: "/Germany", name: "Germany", props: { nation_header: "Germany", nation_query: "germany" }, component: Germany },
  { path: "/France", name: "France", props: { nation_header: "France", nation_query: "france" }, component: France },
  { path: "/Sweden", name: "Sweden", props: { nation_header: "Sweden", nation_query: "sweden" }, component: Sweden },
  { path: "/Japan", name: "Japan", props: { nation_header: "Japan", nation_query: "japan" }, component: Japan },
  { path: "/China", name: "China", props: { nation_header: "China", nation_query: "china" }, component: China },
  { path: "/Czechoslovakia", name: "Czechoslovakia", props: { nation_header: "Czechoslovakia", nation_query: "czech" }, component: Czechoslovakia },
  { path: "/Italy", name: "Italy", props: { nation_header: "Italy", nation_query: "italy" }, component: Italy },
  { path: "/Poland", name: "Poland", props: { nation_header: "Poland", nation_query: "poland" }, component: Poland }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
