<template>
  <div id="container">

    <section v-if="api_error">
      <div id="api_error">
        <p>
          It appears something went wrong when trying to fetch results. Please try again.
        </p>
      </div>
    </section>

    <section v-else-if="loading">
      <div id="loading">
        <LoadingSpinner/>
      </div>
    </section>

    <section v-else id="overlay">

      <div class="nation_header">
        <img id="nation_flag" :src="require('@/assets/nation_flags/' + nation_header + '.png')">
        {{ nation_header }}
      </div>

      <!-- switch to toggle trees -->
      <SwitchTree :trees="trees" :initial_tree="initial_tree">
        <template v-slot:tab_head_tech_tree>
          Tech Tree
        </template>
        <!-- tech tree -->
        <template v-slot:tab_panel_tech_tree>
          <TabCard :tabs="tabs" :initial_tab="initial_tab">
            <!-- Light Tanks -->
            <template v-slot:tab_head_light>
              Light Tanks
            </template>
            <template v-slot:tab_panel_light>
              <NationSlot :parentData="light_tanks"/>
            </template>

            <!-- Medium Tanks -->
            <template v-slot:tab_head_medium>
              Medium Tanks
            </template>
            <template v-slot:tab_panel_medium>
              <NationSlot :parentData="medium_tanks"/>
            </template>

            <!-- Heavy Tanks -->
            <template v-slot:tab_head_heavy>
              Heavy Tanks
            </template>
            <template v-slot:tab_panel_heavy>
              <NationSlot :parentData="heavy_tanks"/>
            </template>

            <!-- TD Tanks -->
            <template v-slot:tab_head_td>
              Tank Destroyers
            </template>
            <template v-slot:tab_panel_td>
              <NationSlot :parentData="tds"/>
            </template>

            <!-- SPG Tanks -->
            <template v-slot:tab_head_spg>
              Artillery
            </template>
            <template v-slot:tab_panel_spg>
              <NationSlot :parentData="spgs"/>
            </template>
          </TabCard>
        </template>

        <!-- premium tree -->
        <template v-slot:tab_head_premium_tree>
          Premium Tree
        </template>
        <template v-slot:tab_panel_premium_tree>

          <TabCard :tabs="tabs" :initial_tab="initial_tab">
            <!-- Light Tanks -->
            <template v-slot:tab_head_light>
              Light Tanks
            </template>
            <template v-slot:tab_panel_light>
              <NationSlot :parentData="p_light_tanks"/>
            </template>

            <!-- Medium Tanks -->
            <template v-slot:tab_head_medium>
              Medium Tanks
            </template>
            <template v-slot:tab_panel_medium>
              <NationSlot :parentData="p_medium_tanks"/>
            </template>

            <!-- Heavy Tanks -->
            <template v-slot:tab_head_heavy>
              Heavy Tanks
            </template>
            <template v-slot:tab_panel_heavy>
              <NationSlot :parentData="p_heavy_tanks"/>
            </template>

            <!-- TD Tanks -->
            <template v-slot:tab_head_td>
              Tank Destroyers
            </template>
            <template v-slot:tab_panel_td>
              <NationSlot :parentData="p_tds"/>
            </template>

            <!-- SPG Tanks -->
            <template v-slot:tab_head_spg>
              Artillery
            </template>
            <template v-slot:tab_panel_spg>
              <NationSlot :parentData="p_spgs"/>
            </template>
          </TabCard>
        </template>

      </SwitchTree>

      <Footer/>

    </section> <!-- overlay -->

 </div>
</template>

<script>
import axios from 'axios'
import LoadingSpinner from '@/components/misc/LoadingSpinner'
import TabCard from './TabCard'
import NationSlot from './NationSlot'
import SwitchTree from './SwitchTrees'
import Footer from '@/views/Footer'

export default {
  name: "BuildTree",
  components: {
    LoadingSpinner,
    SwitchTree,
    TabCard,
    NationSlot,
    Footer
  },
  props: {
    nation_header: String,
    nation_query: String
  },
  data() {
    return {
      api_error: false,
      loading: true,
      all_tanks: null,
      light_tanks: [],
      medium_tanks: [],
      heavy_tanks: [],
      tds: [],
      spgs: [],
      p_light_tanks: [],
      p_medium_tanks: [],
      p_heavy_tanks: [],
      p_tds: [],
      p_spgs: [],
      initial_tree: "tech_tree",
      trees: ["tech_tree", "premium_tree"],

      initial_tab: "light",
      tabs: ["light", "medium", "heavy", "td", "spg"]
    }
  },

  methods: {
    /**
     * fetch the ussr results
     *
    **/
    fetch_ussr_tanks() {
      axios
      .get("https://api.worldoftanks.com/wot/encyclopedia/vehicles/?application_id=7362aa6d7cc838899f3a91d25d76625d&nation=" + this.nation_query
      )
      .then(response => {
        //check search error
        if (this.check_api_error(response.data.status) && response.data.meta.count > 0) {
          //store results
          this.all_tanks = response.data.data
          this.filter_tank_types()
          this.loading = false
        }
        else {
          //handle if error with query
          this.api_error = true
        }
      })
      .catch(error => console.log(error))
    },

    filter_tank_types() {
      for (let tank in this.all_tanks) {
        if (!this.all_tanks[tank]['is_premium']) {
          switch(this.all_tanks[tank]['type']) {
            case "lightTank":
              this.light_tanks.push(this.all_tanks[tank])
              break

            case "mediumTank":
              this.medium_tanks.push(this.all_tanks[tank])
              break

            case "heavyTank":
              this.heavy_tanks.push(this.all_tanks[tank])
              break

            case "AT-SPG":
              this.tds.push(this.all_tanks[tank])
              break

            case "SPG":
              this.spgs.push(this.all_tanks[tank])
              break
          }
        }
        else {
          switch(this.all_tanks[tank]['type']) {
            case "lightTank":
              this.p_light_tanks.push(this.all_tanks[tank])
              break

            case "mediumTank":
              this.p_medium_tanks.push(this.all_tanks[tank])
              break

            case "heavyTank":
              this.p_heavy_tanks.push(this.all_tanks[tank])
              break

            case "AT-SPG":
              this.p_tds.push(this.all_tanks[tank])
              break

            case "SPG":
              this.p_spgs.push(this.all_tanks[tank])
              break
          }
        }
      }
    },

    //check for returned api errors
    check_api_error(the_response) {
      return (the_response === "ok") ? true : false
    }
  },

  mounted() {
    this.fetch_ussr_tanks()
  }
}
</script>

