  padding: 0.5em 3em 1em 2em;
  text-align: left;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 48px;
  font-weight: bold;
  color: #FFF;
  letter-spacing: 4px;
  text-transform: uppercase;
}

