  padding: 1em 0;
  z-index: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 1000px;
  height: auto;
  -webkit-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
  user-drag: none;
}

#api_error {
  padding-top: 3em;
  color: red;
  font-size: 20px;
}
</style>